@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

* {padding:0; margin:0; transition: transform .3s;}
a {text-decoration:none; color:#333; font-family: 'Noto Sans KR', sans-serif;}
ul,ol,li {list-style: none;}
body {font-size: 16px; color:#333; font-family: 'Noto Sans KR', sans-serif;}

/* 2.상단 GNB */
header {
  height: 80px;
  width: 1100px;
  margin: 0 auto;
  background-color: #fff;
  z-index: 99;
  border-bottom: 1px solid #dfdfdf;
}

.act33 {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  box-shadow: 0.5px 0.5px 3px #9c9c9c;
}

div.logo {
  float: left;
  margin-top: 20px;
  margin-left: 10px;
}

div.logo img {
  width: 60px;
  margin-left: 10px;
}

.top-menu {
  margin-top: 25px;
  margin-left: 80px;
  float: left;
  text-align: center;
}

.top-menu>ul>li>a {
  font-weight: bold;
  display: inline-block;
  margin-bottom: 25px;
}

.top-menu>ul {
  display: inline-block;
}

.top-menu>ul>li {
  display: inline-block;
  margin: 0 18px;
}

.top-menu>ul>li a:hover {
  color: #f26c3a;
}
.top-menu>ul>li:hover > a {
  color: #f26c3a;
}

.top-menu li {
  position: relative;
}

.top-menu ul ul {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  border-radius: 10px;
  box-shadow: 1px 1px 3px #9c9c9c;
  z-index: 10;
  background-color: #fff;
}

.top-menu ul ul>li {
  margin: 10px 0;
  padding: 0 25px;
}

.top-menu >ul>li:hover ul {
  display: block;
}

.top-menu a {
  white-space: nowrap;
}

div.top-item {
  float: left;
  width: 150px;
  height: 50px;
  margin: 15px;
  margin-left: 30px;
  background-color: #7cafdf;
  text-align: center;
  border-radius: 30px;
}

div.top-item>a {
  display: inline-block;
  margin-top: 10px;
  font-weight: bold;
  color: #fff; 
}

div.top-item>a:hover {
  color: #333;
}

div.icon {
  float: right;
  margin: 15px;  
}

div.icon a {
  display: inline-block;
  font-size: 28px;
}

div.icon a:hover {
  color: #7cafdf;
}

div.icon a:nth-child(1) {
  margin-right: 20px;
}

div.icon a:nth-child(2) {
  margin-right: 20px;
}

div.icon>li {
  display: inline-block;
}

.top-menu2 >li {
  position: relative;
  padding-bottom: 10px;
  text-align: center;
}
.top-menu2 ul {
  position: absolute;
  top: 100%;
  left: -20px;
  width: 115px;
  display: none;
  border-radius: 10px;
  box-shadow: 1px 1px 3px #9c9c9c;
  z-index: 10;
  background-color: #fff;
}
.top-menu2 ul>li {
  margin: 10px 0;
  padding: 0 25px;
}
.top-menu2 >li:hover ul {
  display: block;
}
.top-menu2 a {
  white-space: nowrap;
}
.top-menu2>li>ul>li>a {
  font-size: 16px;
}
