/* 1.game1 */
#game1 {
  margin: 0 auto;
  width: 1100px;
  height: 480px;
  border: 1px solid #7cafdf;
  margin-bottom: 50px;
}
#game1 h2 {
  margin:30px auto;
  text-align: center;
}
#game1 h2 div {
  background-color: #df1313;
  width: 250px;
  height: 50px;
  line-height: 50px;
  margin: 0 auto;
  border-radius: 10px;
}
#game1 h2 span {
  color : #fff;
  font-weight: bold;
  margin-right: 20px;
}
#top_lo {
  width: 850px;
  height: 120px;
  border-top: 1px solid red;
  margin: 0 auto;
}
#top_lo #balltext {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 18px;
  margin-left: 150px;
}
#middle_lo {
  width: 850px;
  height: 120px;
  margin: 20px auto;
}
#bonustext {
  margin-bottom: 10px;
  font-size: 18px;
  margin-left: 150px;
}
#ball_top {
  margin-left: 250px;
}
#ball_top2 {
  margin-left: 250px;
}
.ball {
  display: inline-block;
  border-radius: 40px; 
  width: 60px;
  height: 60px;
  line-height: 60px;
  font-size: 30px;
  color: #fff;
  text-align: center;
  margin: 0 15px;
  font-weight: 500;

  animation-name: obj2;
  animation-duration: 1s;
  animation-delay: 1.0s;
  animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  animation-fill-mode: both;
}
@keyframes obj2{
  0%{ opacity: 0; transform: translateY(150px) scale(0.5);}
  100%{opacity: 1; transform: translateX(-100px) scale(1);}
}
#game1 #lottobtn {
  text-align: center;
}
#game1 #lottobtn button {
  width: 150px;
  height: 50px;
  background-color: #76afe6;
  border-radius: 20px;
  border: 1px solid #fff;
  font-size: 25px;
  color: #fff;
  cursor: pointer;
}
#game1 #lottobtn button:hover {
  background-color: aqua;
  color: #333;
}






